@use "base/colors" as c;
@use "base/mixins" as m;

.projects {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    row-gap: 2rem;

    @include m.tablet {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 1rem;
        row-gap: 3rem;
    }
    @include m.laptop {
        grid-template-columns: repeat(3, 1fr);
        column-gap: 3rem;
        row-gap: 5rem;
    }
    .project {
        background: rgb(10,36,62);
        background: -moz-linear-gradient(0deg, rgba(10,36,62,0.24) 0%, rgba(0,35,70,0.69) 100%);
        background: -webkit-linear-gradient(0deg, rgba(10,36,62,0.24) 0%, rgba(0,35,70,0.69) 100%);
        background: linear-gradient(0deg, rgba(10,36,62,0.24) 0%, rgba(0,35,70,0.69) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#0a243e",endColorstr="#002346",GradientType=1);

        display: flex;
        flex-direction: column;

        .project__image {
            height: auto;
            padding: .4rem;
        }
        .project__content {
            padding: 2.6rem 2rem;
            display: flex;
            flex-direction: column;
            height: 100%;
        }
        h3 {
            font-weight: 700;
            margin-bottom: 2.6rem;
        }
        .tags {
            text-align: center;
            color: c.$grey;
            margin: 2.6rem 0;
        }
        .project__icons {
            display: flex;
            justify-content: space-around;
            padding: 0 2rem;
            margin-top: auto;
        }
        .project__icon {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-decoration: none;
            p{color: lighten($color: c.$orange, $amount: 20%);}
        }
    }
}