/* 10px = 1rem */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 16px;
  background-color: red;
  background: rgb(64, 64, 64);
  background: -moz-linear-gradient(9deg, rgb(64, 64, 64) -15%, rgb(74, 97, 119) 100%);
  background: -webkit-linear-gradient(9deg, rgb(64, 64, 64) -15%, rgb(74, 97, 119) 100%);
  background: linear-gradient(9deg, rgb(64, 64, 64) 0%, rgb(74, 97, 119) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#404040",endColorstr="#4a6177",GradientType=1);
}

img {
  display: block;
  width: 100%;
}

input:not([type=submit]) {
  padding: 4px;
}

nav {
  width: 100%;
  display: flex;
  flex-direction: column;
}
@media (min-width: 481px) {
  nav {
    width: auto;
    flex-direction: row;
    align-items: center;
  }
}
nav a {
  text-decoration: none;
  color: inherit;
  text-align: center;
  font-size: 1.8rem;
}
nav a:not(:last-child) {
  margin-bottom: 1.2rem;
}
@media (min-width: 481px) {
  nav a:not(:last-child) {
    margin-bottom: 0;
    margin-right: 3rem;
  }
}
nav a:before {
  display: block;
  content: attr(data-hover);
  font-weight: 700;
  height: 0;
  visibility: hidden;
}
nav a:hover {
  font-weight: 700;
  color: #EC7E57;
}

body {
  font-family: "Nunito Sans", sans-serif;
}

h1, h2, h3 {
  font-family: "Cormorant Garamond", serif;
  text-align: center;
  margin: 0 0 4rem 0;
  font-weight: 400;
}

h1 {
  font-size: 3.6rem;
  text-transform: uppercase;
  color: #EC7E57;
  font-weight: 700;
  margin-bottom: 1.4rem;
}
@media (min-width: 481px) {
  h1 {
    font-size: 6rem;
  }
}

h2 {
  font-size: 3.4rem;
  color: white;
}
h2.h1subtitle {
  font-size: 2.2rem;
  margin: 0;
}
@media (min-width: 481px) {
  h2.h1subtitle {
    font-size: 2.8rem;
  }
}

h3 {
  font-size: 3rem;
  color: white;
}

p {
  letter-spacing: 0.4px;
  margin: 1.4rem 0;
  font-size: 2rem;
  color: white;
}

.container, .container-sm {
  margin: 0 auto;
  padding: 4rem 0;
  max-width: 1200px;
  width: 98%;
}

.container-sm {
  max-width: 700px;
}

.ucase {
  text-transform: uppercase;
}

.hidden {
  display: none !important;
}

.invisible {
  visibility: hidden !important;
}

.transparent {
  opacity: 0 !important;
}

.no-scroll {
  overflow: hidden !important;
}

.button {
  width: 100%;
  margin-top: 2rem;
  padding: 0.7rem 1.5rem;
  border: none;
  border-radius: 99px;
  font-size: 1.6rem;
  letter-spacing: 1px;
  font-weight: bold;
  text-align: center;
  background-color: rebeccapurple;
  color: white;
  transition: background-color 0.3s ease;
}
.button:hover {
  background-color: #8c53c6;
  cursor: pointer;
}
@media (min-width: 481px) {
  .button {
    width: auto;
  }
}

header {
  background-size: 140%;
  background-position: bottom;
  background-color: #091C2E;
  padding: 6rem 2rem;
}
@media (min-width: 481px) {
  header {
    background-size: cover;
    background-position: center;
    height: 36rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.webp header {
  background-image: url(../img/heroMobile.webp);
}
@media (min-width: 900px) {
  .webp header {
    background-image: url(../img/hero.webp);
  }
}
.no-webp header {
  background-image: url(../img/heroMobile.jpg);
}
@media (min-width: 900px) {
  .no-webp header {
    background-image: url(../img/hero.jpg);
  }
}

.nav-fill {
  display: block;
  height: 6rem;
}

.nav {
  margin-top: -6rem;
  height: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #091C2E;
}
.nav .container {
  padding: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.nav .container.nav-centered {
  justify-content: center;
}
.nav .container a {
  text-decoration: none;
}
.nav .container h2 {
  color: #EC7E57;
  text-transform: uppercase;
  margin: 0;
  font-weight: 700;
  font-size: 2.4rem;
}
@media (min-width: 481px) {
  .nav .container h2 {
    font-size: 2.8rem;
  }
}
.nav .container nav {
  color: white;
  display: none;
}
@media (min-width: 481px) {
  .nav .container nav {
    display: flex;
  }
}
.nav .container .menu-mobile {
  display: block;
}
@media (min-width: 481px) {
  .nav .container .menu-mobile {
    display: none;
  }
}
.nav.nav-fixed {
  position: fixed;
  top: 0;
  width: 100%;
  margin: 0;
}

.menu-mobile-modal {
  background-color: #091C2E;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.menu-mobile-modal nav {
  color: white;
}
.menu-mobile-modal nav a {
  margin-bottom: 8vh;
}
.menu-mobile-modal svg {
  margin-bottom: 10vh;
  display: block;
}
@media (min-width: 481px) {
  .menu-mobile-modal {
    display: none;
  }
}

footer {
  background-color: rgb(35, 35, 35);
  color: white;
}

#footer .copy {
  text-align: center;
}

.projects {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 2rem;
}
@media (min-width: 481px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem;
    row-gap: 3rem;
  }
}
@media (min-width: 1025px) {
  .projects {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 3rem;
    row-gap: 5rem;
  }
}
.projects .project {
  background: rgb(10, 36, 62);
  background: -moz-linear-gradient(0deg, rgba(10, 36, 62, 0.24) 0%, rgba(0, 35, 70, 0.69) 100%);
  background: -webkit-linear-gradient(0deg, rgba(10, 36, 62, 0.24) 0%, rgba(0, 35, 70, 0.69) 100%);
  background: linear-gradient(0deg, rgba(10, 36, 62, 0.24) 0%, rgba(0, 35, 70, 0.69) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#0a243e",endColorstr="#002346",GradientType=1);
  display: flex;
  flex-direction: column;
}
.projects .project .project__image {
  height: auto;
  padding: 0.4rem;
}
.projects .project .project__content {
  padding: 2.6rem 2rem;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.projects .project h3 {
  font-weight: 700;
  margin-bottom: 2.6rem;
}
.projects .project .tags {
  text-align: center;
  color: #D4D4D4;
  margin: 2.6rem 0;
}
.projects .project .project__icons {
  display: flex;
  justify-content: space-around;
  padding: 0 2rem;
  margin-top: auto;
}
.projects .project .project__icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
}
.projects .project .project__icon p {
  color: #f6c4b3;
}